/* App.css */

/* Reset some default styles */
body {
  margin: 0;
  font-family: 
  -apple-system, 
  BlinkMacSystemFont, 
  'Segoe UI', Roboto, 
  'Helvetica Neue', 
  Arial, 
  sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* Main container styles */
.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-color: #f0f0f0;
}

/* Optional: Add more styles as needed for your application */
